import React from 'react';
import icone1 from '../../images/Emoticon/icons8-itunes-36.png';
const Footer = () => {
    return (
        <div className='container mx-auto flex flex-col md:flex-row justify-between'>
            <div className='flex mb-4'>
                <img src={icone1} alt="logo" />
                <div className='mr-3'>
                    <p className='text-white text-base'>تمامی حقوق برای رسانه <span className='text-iconetopmmenu'>موزیکفا</span> محفوظ می باشد.</p>
                    <p className='text-white text-base'>طراحی و پشتیبانی : <span className='text-iconetopmmenu'>وبیت</span> </p>
                </div>
            </div>
            <div className='flex justify-center md:justify-start'>
                <div className='ml-3 mt-1'>
                    <p className='text-white text-xs'>COMMENTS : <span className='text-iconetopmmenu'>35253</span> </p>
                    <p className='text-white text-xs'>ALL SONGS : <span className='text-iconetopmmenu'>7,645</span> </p>
                </div>
                <button className='text-white bg-black w-20 h-9 text-base rounded-lg'>contact</button>
            </div>
        </div>
    );
};

export default Footer;