import React from 'react';

type ParagraphType = {
    title?:string;
    className?:string;
    textalignment?:'text-left'|'text-center'|'text-right'|undefined;
}
const Paragraph = (props:ParagraphType) => {
    const className = props.className + ' ' + props.textalignment;
    return (
      <p className={className}>{props.title}</p>
    );
};

export default Paragraph;