import React from 'react';
import icone1 from '../../images/Emoticon/icons8-itunes-62.png';
import Paragraph from '../paragraph/paragraph.component';

type ProfileTitleType = {
    title?: string;
    paragraph?: string;
    classNameTitle?: string;
    classNameParagraph?: string;
    border?: string;
}
const ProfileTitle = (props: ProfileTitleType) => {
    return (
        <div className='flex gap-2'>
            {props.border &&
                <div className='w-1.5 bg-iconetopmmenu'></div>
            }
            <div>
                <img src={icone1} alt="Logo-Muzic" />
            </div>
            <div>
                <Paragraph title={props.title} className={props.classNameTitle} />
                <Paragraph title={props.paragraph} className={props.classNameParagraph} />
            </div>
        </div>
    );
};

export default ProfileTitle;