import React from 'react';
import Paragraph from '../paragraph/paragraph.component';
const TopMenu = () => {
    const menu = [
        { title: 'آهنگ های جدید', bottomTitle: 'New Tracks' },
        { title: 'آهنگ های ویژه', bottomTitle: 'Special Songs' },
        { title: 'آهنگ های شاد', bottomTitle: 'Happy Songs' },
        { title: 'آهنگ های ریمیکس', bottomTitle: 'Remix Songs' },
        { title: 'دانلود مداحی', bottomTitle: 'Nohe Madahi' },
    ]
    return (
        <div className="container flex flex-col mx-auto bg-topmenu md:flex-row">
            <div className='bg-iconetopmmenu p-6'>
            <Paragraph textalignment='text-center' title='موزیک فا' className='text-5xl text-white' />
            </div>
            <div className='flex flex-wrap gap-6  col-span-3 p-6'>
                {menu.map(item => {
                    return (
                        <a className='link-top-menu' href="#">
                            <Paragraph title={item.title} className='text-base p-top-menu text-white' />
                            <Paragraph textalignment='text-center' title={item.bottomTitle} className='text-xs text-white' />
                        </a>
                    )
                })}
            </div>
        </div>
    );
};

export default TopMenu;