import React from 'react';
import Paragraph from '../paragraph/paragraph.component';
import icone1 from '../../images/Emoticon/Untitled-1.jpg';
const Row1Main = () => {
    return (
        <div className='border-8 border-border p-4 bg-white'>
            <Paragraph textalignment='text-center' title='دانلود آهنگ جدید' className='text-2xl text-iconetopmmenu' />
            <Paragraph textalignment='text-center' title='اهنگ جدید MP3 | دانلود بهترین آهنگ های جدید ایرانی با لینک مستقیم و کیفیت عالی از' className='text-sm title-small' />
            <Paragraph textalignment='text-center' title='موزیکفا همراه با متن و کیفیت 320 و 128 هر آهنگ' className='text-sm title-small' />
            <img width='128px' height='128px' className='m-auto' src={icone1} alt="logo" />
            <Paragraph textalignment='text-center' title='موزیک های معروف خواننده های ایرانی' className='mt-2 text-sm title-small' />
            <Paragraph textalignment='text-center' title='آهنگ های شاد / جدید / قدیمی / غمگین / فول آرشیو / پاپ' className='text-sm title-small' />

        </div>
    );
};

export default Row1Main;