import React from 'react';
import Paragraph from '../paragraph/paragraph.component';
import icone1 from '../../images/Emoticon/tell.svg';
const BottomMenu = () => {
    return (
        <div className='container flex flex-col mx-auto bg-topmenu md:flex-row md:justify-between'>
            <div className='p-6'>
                    <Paragraph title='موزیکفا تابع قوانین جمهوری اسلامی ایران می‌باشد.' className='text-lg text-white' />
                    <Paragraph title='جهت پخش آثار موسیقی خود تماس بگیرید' className='text-lg text-white' />
            </div>
            <div className='flex relative gap-4 flex-wrap md:flex-nowrap md:w-7/12 p-6'>
                <button className='w-full md:w-1/4 bg-input rounded-md h-16'><img className='m-auto' src={icone1} alt="telegram-tel" /> </button>
                <input  placeholder="جستجوی اسم خواننده یا آهنگ ..." type="text" className='w-full text-sm p-4 md:w-3/4 bg-input rounded-md h-16 focus:outline-none focus:ring-2 focus:ring-blue-600'/>
               <button className='text-white p-5 bg-buttoninput absolute left-10 top-6'>جستجو</button>
            </div>
        </div>
    );
};

export default BottomMenu;