import React from 'react';
import Paragraph from '../paragraph/paragraph.component';

const Row4Mainleft = () => {
    const data = [
        { title: 'دانلود آهنگ مجید خراطها آرایش ساده' },
        { title: 'دانلود آهنگ مهریار گزینه ها' },
        { title: 'دانلود آهنگ عاشق یکی شدم نمیتونم بگم بهش' },
        { title: 'دانلود آهنگ اینا تورو اذیت میکنن نیومده خستت میکنن مجید رضوی' },
        { title: 'دانلود آهنگ زانکو گوشی خاموش' },
        { title: 'دانلود آهنگ مهریار امان' },
        { title: 'دانلود آهنگ کسری زاهدی لیلی زیبای من' },
        { title: 'دانلود آهنگ محسن چاوشی ما بزرگ و نادانیم' },
    ]
    return (
        <div className='border-8 border-border bg-white'>
            <div className='flex mt-6 border-t-2 border-b-2'>
                <div className='w-1.5 bg-border'></div>
                <Paragraph title='در انتظار پخش...' className='text-xl text-black mr-2 mt-2 mb-2' />
            </div>
            <div className='mt-6 pb-6'>
            {data.map(item => {
                return (
                    <div className='flex mt-3'>
                    <a className='flex link-row1-left' href='#'>
                    <div className='w-1.5 bg-border khat'></div>
                    <Paragraph title={item.title} className='text-base title-small mr-2 p-row1-left' />
                    </a>
                </div>
                )
            })}
            </div>
        </div>
    );
};

export default Row4Mainleft;