import React from 'react';
import image1 from '../../images/Emoticon/images1.jpg';
import image3 from '../../images/Emoticon/iamges3.jpg';
import image4 from '../../images/Emoticon/images4.jpg';
import image6 from '../../images/Emoticon/images6.jpg';
import image7 from '../../images/Emoticon/images7.jpg';
import Paragraph from '../paragraph/paragraph.component';
const RowImages = () => {
    const images = [
        { src: image1, title: 'محمدرضا فیضی ', paragraph: 'آدم معمولی' },
        { src: image4, title: 'یاسان', paragraph: 'لیلا' },
        { src: image3, title: 'آریا زند ', paragraph: 'لعنت به تقدیر' },
        { src: image4, title: 'دانلود آهنگ تولدت مبارک', paragraph: '' },
        { src: image1, title: 'یوسف زمانی ', paragraph: 'آرامش ' },
        { src: image6, title: 'علیرضا طلیسچی ', paragraph: 'خاطره' },
        { src: image7, title: 'رامیراد ', paragraph: 'کمیاب' },
        { src: image6, title: 'احمد سعیدی ', paragraph: 'دریا' },
    ]
    return (
        <div className='container grid grid-cols-2 md:grid-cols-8 mx-auto border-8 border-border'>
            {images.map(item => {
                return (
                    <a href='#' className='relative m-auto row-iamges'>
                        <img src={item.src} alt="images" />
                        <div className='absolute bottom-0 bg-kadr-images w-full'>
                        <Paragraph textalignment='text-center' title={item.title} className='text-base text-white' />
                        <Paragraph textalignment='text-center' title={item.paragraph} className='text-sm text-white' />
                        </div>
                    </a>
                )
            })}
        </div>
    );
};

export default RowImages;