import React from 'react';
import Paragraph from '../paragraph/paragraph.component';
import ProfileTitle from '../profile-title/profile-title';
import ReactAudioPlayer from 'react-audio-player';

type model = {
    title?: string;
    paragraph?:string;
    paragraph1?:string;
    paragraph2?:string;
    paragraph3?:string;
    paragraph4?:string;
    paragraph5?:string;
    titleButton?:string;
    src?:string;
}
type Row2MainType = {
    model: model[]
}
const Row2Main = (props: Row2MainType) => {
    return (
        <>
            {props.model.map(item => {
                return (
                    <div className='border-8 border-border bg-white'>
                        <div className='mt-4 mr-4'>
                        <ProfileTitle title={item.title} paragraph={item.paragraph} classNameParagraph='text-sm title-small' classNameTitle='text-xl mt-1 text-iconetopmmenu' />
                        </div>
                        <div className='w-3/4 h-0.5 bg-border mt-4'></div>
                        <div className='p-4'>
                        <Paragraph textalignment='text-center' title={item.paragraph1} className='text-lg title-small' />
                        <Paragraph textalignment='text-center' title={item.paragraph2} className='text-base title-small mt-1' />
                        <Paragraph textalignment='text-center' title={item.paragraph3} className='text-base title-small mt-1' />
                        <Paragraph textalignment='text-center' title={item.paragraph4} className='text-base title-small mt-1' />
                        <Paragraph textalignment='text-center' title={item.paragraph5} className='text-base title-small mt-1 mb-4' />
                        <img className='m-auto rounded-md' src={item.src} alt="album" />
                        <ReactAudioPlayer className='w-full mt-4'
                            src="my_audio_file.ogg"
                            autoPlay
                            controls
                        />
                        </div>
                        <button className='w-full bg-iconetopmmenu p-6 text-lg mt-4 text-white'>{item.titleButton}</button>
                    </div>
                )
            }
            )

            }
        </>
    );
};

export default Row2Main;