import React from 'react';
import Paragraph from '../paragraph/paragraph.component';

const Row3Mainleft = () => {
    const data = [
        { title: 'دانلود نوحه امشب شهادتنامه عشاق امضا میشود' },
        { title: 'دانلود مداحی گرم جنونم حسین مجید بنی فاطمه' },
        { title: 'دانلود مداحی مجید بنی فاطمه دهه اول محرم 1400' },
        { title: 'دانلود مداحی میثم مطیعی دهه اول محرم 1400' },
        { title: 'دانلود نوحه خداحافظ ای برادر زینب مهدی رسولی' },
        { title: 'دانلود نوحه هرکه دارد هوس کرببلا بسم الله محمود' },
        { title: 'دانلود آهنگ مجید خراطها آرایش ساده' },
        { title: 'دانلود نوحه دل خون تر از ابر و طوفان محمود کریمی' },
    ]
    return (
        <div className='border-8 border-border bg-white'>
            <div className='flex mt-6 border-t-2 border-b-2'>
                <div className='w-1.5 bg-border'></div>
                <Paragraph title='مداحی ها' className='text-xl text-black mr-2 mt-2 mb-2' />
            </div>
            <div className='mt-6 pb-6'>
            {data.map(item => {
                return (
                    <div className='flex mt-3'>
                    <a className='flex link-row1-left' href='#'>
                    <div className='w-1.5 bg-border khat'></div>
                    <Paragraph title={item.title} className='text-base title-small mr-2 p-row1-left' />
                    </a>
                </div>
                )
            })}
            </div>
        </div>
    );
};

export default Row3Mainleft;